






import Vue from "vue";
import tmi from "twitch-js"

export default Vue.extend({
  name: "ChatTest",
  props: {
    msg: String
  },
  data() {
    return {};
  },
  mounted() {
    console.log("mounted");
    let options = {
      options:{
        debug: false
      },
      connection:{
        secure: true
      },
      identity: {
        uername: "justinfan1",
        password: ""
      },
      channels: ["esl_csgo"]
    };
    let client =  new tmi.client(options);
    client.on("message", (target: any, context: any, msg: any, self: any) => {
      console.log(msg); 
    });

    client.on("connected", (addr, port) => { console.log(`* Connected to ${addr}:${port}`);
    });

    client.connect();
  }
});
